 *    overcome whitespace issues when using inline-block.
 */
.o-grid__item {
  position: relative;
  /* [1] */
  -webkit-box-sizing: border-box;
  /* [2] */
  -moz-box-sizing: border-box;
  /* [2] */
  box-sizing: border-box;
  /* [2] */
  display: inline-block;
  /* [3] */
  width: 100%;
  /* [4] */
  min-height: 1px;
  /* [5] */
  padding-left: 1rem;
  /* [6] */
  vertical-align: top;
  /* [7] */
  text-align: left;
  /* |8] */
  font-size: 1rem;
  /* [9] */ }

